import React, { ReactNode } from 'react';
import s from './SettingsPage.module.scss';
import { NavLink } from 'react-router-dom';
import { AppRoutes } from 'pagesReact';

const tabs = [
  { id: 1, path: AppRoutes.SETTINGS_TAB_GENERAL, text: 'General' },
  { id: 2, path: AppRoutes.SETTINGS_TAB_PROFILE, text: 'Profile' },
  { id: 3, path: AppRoutes.SETTINGS_TAB_PASSWORD, text: 'Password' },
  { id: 4, path: AppRoutes.SETTINGS_TAB_PLAN, text: 'Plan&Billing' },
];

type PropsT = {
  children: ReactNode;
}

const SettingsPage = ({
  children,
}: PropsT) => {

  return (
    <div className={s.container}>
      <h1 className={s.title}>Settings</h1>

      <div className={s.tabsContainer}>
        {tabs.map(tab => {
          return (
            <NavLink
              key={tab.id}
              to={tab.path}
              className={({ isActive }) => `${s.tab} ${isActive && s.tabActive}`}
            >
              {tab.text}
            </NavLink>
          );
        })}
      </div>

      {children}
    </div>
  );
};

export default SettingsPage;